.radio {
  appearance: none;

  &:checked ~ .radio__fake::before {
    width: 16px;
    height: 16px;
  }
}

.radio__fake {
  display: flex;
  width: 46px;
  height: 46px;
  border-radius: 50%;
  background-color: rgba(242, 242, 242, 1);
  align-items: center;
  justify-content: center;
  margin-left: -16px;

  &::before {
    content: "";
    display: block;
    width: 0;
    height: 0;
    border-radius: 50%;
    background-color: #333333;
  }

  &:hover {
    background-color: rgba(229, 229, 229, 1);
  }

  &:focus,
  &:focus-within {
    background-color: rgba(255, 255, 255, 1);
    outline: 0;
    outline-offset: 0;
  }
}
